import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { RoutedTabs, NavTab } from "react-router-tabs";

// with default styles:
// import "styles/react-router-tabs.css";

const UsersPage = ({ match }) => {
    return (
        <div>
            <NavTab to="/admins">Admins</NavTab>
            <NavTab to="/moderators">Moderators</NavTab>
            <NavTab to="/users">Users</NavTab>

            <Switch>
                <Route
                    exact
                    path={`${match.path}`}
                    render={() => <Redirect replace to={`${match.path}/admins`} />}
                />
                <Route path={`${match.path}/admins`} component={<div>admins</div>} />
                <Route path={`${match.path}/moderators`} component={<div>mods</div>} />
                <Route path={`${match.path}/users`} component={<div>users</div>} />
            </Switch>
        </div>
    );
};

export default UsersPage;